.body-container {
  position: relative;
  width: 100%;
  padding: 20px;
  border-bottom: 5px solid #000;
  border-top: 5px solid #000;
}

.search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 37px; 
  justify-content: center;
  padding: 20px;
}
.search-container h3 {
  font-weight: 700;
  font-size: 38px;
  margin-bottom: 50px;
  text-align: center;
  
}
.search-container span {
  color: #1F51FF;
}
.search-box {
  position: relative;
  margin-bottom: 72px;

}
.search-input {
  font-weight: 700;
  border: none;
  border-radius: 4px;
  width: 750px;
  background-color: #fff;
  border-radius: 4px;
}
.search-btn {
  background-color: #1F51FF !important;
  color: #fff !important;
  text-transform: none;
  width: 173px;
  height: 56px;
  position: absolute;
  right: 0px;
  font-size: 20px;
}
.search-btn:hover {
  background-color: #090909 !important;
  
}

@media screen and (max-width: 1150px) {

.search-container h3 {
  font-size: 30px;
 }
.search-box {
  display: inline-flex;
 }
.search-input {
  width: 300px;
  
 }
.search-btn {
  width: 80px;
  font-size: 14px;
 }
}